<template>
	<view class="container">
		<uni-row class="demo-uni-row">
			<uni-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6" >
				<view class="demo-uni-col dark">

							<uni-title type="h1" title="历史记录" color="#027fff"></uni-title>
							<uni-list :border=true>
										<uni-list-item  :title="(item.ws_url)" :show-badge="true" :badge-text="(item.count)" v-for="(item,index) in history" ></uni-list-item>
																				
									</uni-list>		

				</view>
				
			</uni-col>
			<uni-col :xs="1" :sm="1" :md="1" :lg="1" :xl="1">
			</uni-col>
			<uni-col :xs="17" :sm="17" :md="17" :lg="17" :xl="17">
						<view>
							<view class="demo-uni-col light">
								<uni-title type="h1" title="测试操作区" color="#027fff"></uni-title>
							</view>
							<uni-row class="demo-uni-row">
								<uni-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12" >
									<uni-easyinput prefixIcon="paperclip"  type="text" v-model="connect.ws_url" placeholder="请输入ws地址" />
					
								</uni-col>
								<uni-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4">
									<uni-easyinput type="number"  v-model="connect.count" placeholder="并发量" />				
								</uni-col>
								<uni-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4">
										<button style="background-color: #28a745;height: 38px;color:white;line-height: 38px;;" @click="connectFunc" >连接</button>
								</uni-col>
								<uni-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4"  style="padding-left: 20px;padding-top: 3px;">
										<uni-tag :text="'成功:'+(successConn)" type="success" :circle="true"></uni-tag>
								</uni-col>
								
							</uni-row>
						</view>
	
						<view style="padding-top:40rpx ;">
								<uni-row class="demo-uni-row" >
									
									<uni-col :xs="11" :sm="11" :md="11" :lg="11" :xl="11">
										<view class="demo-uni-col light">
											<uni-title type="h1" title="发送区域(不支持并发调试)" color="#027fff"></uni-title>
										</view>
										<view>
											
											<uni-row class="demo-uni-row" style="background-color: #ced4da;border-top: #808080 1px solid;border-bottom: #808080 1px solid;">

												<uni-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4" class="ping-border" >
												每隔
												</uni-col>
												<uni-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4" class="ping-border">
													 <input type="number"  v-model="s" placeholder="1" style="text-align: center;" />
												</uni-col>
												<uni-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6" class="ping-border">
												秒发送心跳
												</uni-col>
												<uni-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4" class="ping-border">
												Ping
												</uni-col>
												<uni-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6" class="ping-border">
													<button style="background-color: #28a745;height: 28px;color:white;line-height: 28px;border-radius: 0;" >执行Ping</button>
												</uni-col>
											</uni-row>
		
										</view>		
										<view style="padding-top: 10rpx;">
											<uni-easyinput type="textarea" autoHeight style="background-color:#ced4da; " v-model="content" placeholder="发送到服务器的数据包内容"></uni-easyinput>
										</view>
										<view style="padding-top: 10rpx;">
											       <button class="button" type="primary" style="background-color: #28a745;" @click="sendMsg">发送</button>
										</view>
										
										<view class="demo-uni-col light" style="padding-top: 20rpx;">
											<uni-title type="h1" title="发送记录" color="#027fff"></uni-title>
										</view>
										<view class="demo-uni-col light" style="padding-top: 20rpx;">
											<hr>
										</view>
										<view class="demo-uni-col light" style="padding-top: 20rpx;height: 300px; overflow-y:auto; overflow-x:auto;">
											<view v-for="val,index in sendbox">
												<span style="color: #28a745;">{{val.time_}}	 => </span> <pre> {{val.msg}} </pre>
											</view>
	
										</view>
									</uni-col>
									<uni-col :xs="2" :sm="2" :md="2" :lg="2" :xl="2" style="text-align: center;">
										<view class="line"></view>
									</uni-col>
									<uni-col :xs="11" :sm="11" :md="11" :lg="11" :xl="11">
											<view class="demo-uni-col light">
												<uni-title type="h1" title="消息接收区" color="#027fff"></uni-title>
											</view>
											<view class="demo-uni-col light">
												   
												<checkbox-group>
													<label>
														<checkbox value="cb" />json解码
													</label>
													<label>
														<checkbox value="cb" />暂停显示新消息
													</label>
													<label>
														<checkbox value="cb" />只显示最新消息
													</label>
												</checkbox-group>
											</view>
											<view class="demo-uni-col light" style="padding-top: 20rpx;height: 630px; overflow-y:auto; overflow-x:auto;">
												<view v-for="val,index in msgbox">
													<span style="color: #28a745;">{{val.time_}} => </span> 
													
													<pre>
														{{val.msg}}	
													</pre>
												</view>

											</view>
									</uni-col>
								</uni-row>
						</view>
			</uni-col>
		</uni-row>
		<uni-popup ref="popup" type="message">
			<uni-popup-message type="error" message="errorMsg" :duration="2000"></uni-popup-message>
		</uni-popup>
	</view>
</template>

<script>
	var socketOpen = false;
	var socketMsgQueue = [];
	Date.prototype.Format = function (fmt) { // author: meizz
	  var o = {
	    "M+": this.getMonth() + 1, // 月份
	    "d+": this.getDate(), // 日
	    "h+": this.getHours(), // 小时
	    "m+": this.getMinutes(), // 分
	    "s+": this.getSeconds(), // 秒
	    "q+": Math.floor((this.getMonth() + 3) / 3), // 季度
	    "S": this.getMilliseconds() // 毫秒
	  };
	  if (/(y+)/.test(fmt))
	    fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
	  for (var k in o)
	    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
	      return fmt;
	}
	export default {
		
		
		data() {
			return {
				href: 'https://uniapp.dcloud.io/component/README?id=uniui',
				connect:{
					ws_url:"ws://192.168.6.49:8083/ws",
					count:1000,
				},
				res:{
				
						"error": {
							"root_cause": [{
								"type": "circuit_breaking_exception",
								"reason": "[parent] Data too large, data for [<http_request>] would be [2064090112/1.9gb], which is larger than the limit of [2040109465/1.8gb], real usage: [2064090112/1.9gb], new bytes reserved: [0/0b], usages [request=0/0b, fielddata=0/0b, in_flight_requests=0/0b, accounting=78625/76.7kb]",
								"bytes_wanted": 2064090112,
								"bytes_limit": 2040109465,
								"durability": "PERMANENT"
							}],
							"type": "circuit_breaking_exception",
							"reason": "[parent] Data too large, data for [<http_request>] would be [2064090112/1.9gb], which is larger than the limit of [2040109465/1.8gb], real usage: [2064090112/1.9gb], new bytes reserved: [0/0b], usages [request=0/0b, fielddata=0/0b, in_flight_requests=0/0b, accounting=78625/76.7kb]",
							"bytes_wanted": 2064090112,
							"bytes_limit": 2040109465,
							"durability": "PERMANENT"
						},
						"status": 429
					
				},
				value:"",
				s:1,
				msgbox:[],
				sendbox:[],
				content:"",
				successConn:0,
				cacheKey:"ws_url_list",
				history:[],
				errorMsg:"",
			}
		},
		created() {
			this.getCache()
		},
		methods: {
			sendMsg(){
				if(this.content == ""){
					this.errorMsg = "请输入内容";
					this.$refs.popup.open()
					return ;
				}
		
				var time = new Date().Format("yyyy-MM-dd hh:mm:ss");
				this.sendbox.push({msg:this.content,time_:time})
				uni.sendSocketMessage({data: this.content});
			
			},
			connectFunc(){
					let this_ = this;
					this_.successConn = 0;
					
		
						for(let i=1;i<= this_.connect.count;i++){
							try{
								var socketTask = uni.connectSocket({
									url: this.connect.ws_url,
									header:{
										'origin':'127.0.0.1'
									},
									complete: ()=> {}
								});
								socketTask.onOpen(function(){
									   this_.successConn += 1;
		
								})
								
								// let obj = new WebSocket(this.connect.ws_url);
								// obj.onopen = function (msg) {				
								//   console.log('连接成功')			
								// };
							
							}catch(e){
								// console.log(e)
							}
							
						}
						

					

					// uni.onSocketMessage(function (res) {
					// 		var time2 = new Date().Format("yyyy-MM-dd hh:mm:ss");
					// 	this_.msgbox.push({msg:res.data,time_:time2});
					//   console.log('收到服务器内容：' + res.data);
					// });
					// uni.onSocketClose(function (res) {
					//   console.log('WebSocket 已关闭！');
					// });
			},
			setCache(){
				
				let data = [];
				
				try {
				    const value = uni.getStorageSync(this.cacheKey);
				    if (value) {
				        let isAdd = 1;
						value.forEach((elem, index) => {
							if(elem.ws_url == this.connect.ws_url){
								isAdd = 0;
								elem.count = Number(elem.count);
								elem.count += 1;
								elem.count = elem.count.toString();
							}
						 	console.log(elem, elem.ws_url , this.connect.ws_url);
						});
					
						data = value;
						if(isAdd){
							data.push({count:"1",ws_url:this.connect.ws_url})
						}
				    }else{
						data.push({count:"1",ws_url:this.connect.ws_url})
					
					}
				   uni.setStorageSync(this.cacheKey, data);
					this.getCache();
				} catch (e) {
				    // error
						console.log('get:err',e)
				}
		
		
			},
			getCache(){
				 try {
				     const value = uni.getStorageSync(this.cacheKey);
				     if (value) {
				         this.history = value;
				     }
				 } catch (e) {
				     // error
				 }
			}
		},

	}
</script>

<style>
	.container {
		padding: 20px;
		font-size: 14px;
		line-height: 24px;
	}
	.title{
		background-color: #007AFF;
		height: 53px;
	}
	.chat-custom-text {
	    font-size: 12px;
	    color: red;
	}
	.ping-border{
		
		border-left: #808080 1px solid;text-align: center;
	}
	.line{
	 	background:#E7E7E7;/*背景色为浅灰色*/
		width:0.6px;/*设置宽高*/
		height:730px;
		position:relative;/*调整位置*/
		left:30px;
		float:left;/*让此div与前面的并排显示*/
	}

</style>
